import React from 'react';
import { Row, Col }  from 'react-bootstrap';
import resources from '../../resources';

function Details  ()  {
    let details_content = resources.about.details.map((data, index) => {
            return (
                <Col key={index} lg={{span: 4}} md={{span: 6}} sm={{span: 12}} xs={{span: 12}}>
                    <div className={`contact-details-box details-box-${index+1}`}>
                        <h4>{data.info_1}</h4>
                        <p>{data.info_2}<br/>{data.info_3}</p>
                    </div>
                </Col>
            );
        }
    );
    return (
        <div className="contact-details">
            <Row>
                { details_content }
            </Row>
        </div>
    );

};
export default Details;