import React from 'react';
import { Row, Col} from 'react-bootstrap';
import resources from '../../resources';

function Experience (){
    let experience_content = resources.about.experience.map((data, index) => {
            return (
                <Col  key={index} lg={{span: 6}} md={{span: 12}} sm={{span: 12}} xs={{span: 12}}>
                    <div className="about-experience-box">
                        <div className="about-experience-year">
                            <h1>{data.year}</h1>
                            <h4>{data.date}</h4>
                        </div>
                        <div className="about-experience-info">
                            <h4>{data.specialty}</h4>
                            <span>{data.company}</span>
                            <p>{data.desc}</p>
                        </div>
                    </div>
                </Col>
            );
        }
    );
    return (
        <div className="about-experience pb-60">
            <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                <div className="heading-part-content">
                    <div className="heading-part">
                        <h1>Experience</h1>
                        <p>involvement</p>
                    </div>
                </div>
                <div className="about-experience-section">
                    <Row>
                        { experience_content }
                    </Row>
                </div>
            </Col>
        </div>
    );
}

export default Experience;