import React, { useState } from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import resources from '../../resources';

function Slider () {
    let init = [
            'active',
            '',
            '',
            '',
        ];
    const [box,setBox] = useState(init);

    const selectSkill = (index) => {
        setBox(box => {
            box = ['','','',''];
            box[index] = 'active';
            return box;
        });
    };
    let slider_content = resources.skills.technologies.map((data, index) => {
            return (
                <div key={index} className={`services-slider-text ${box[index]}`}  id={index + 1} onClick={() => selectSkill(index)}>
                    <div className={"services-icons"}>
                        <Row>
                            <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}} className={"icon-main"}>
                                <img src={data.img_1} alt="Service icon" />
                            </Col>
                        </Row>
                        <Row>
                            <Col lg={{span:6}} md={{span:6}} sm={{span:6}} xs={{span:6}} className={"icon-secondary"}>
                                <img src={data.img_2} alt="Service icon" />
                            </Col>
                            <Col lg={{span:6}} md={{span:6}} sm={{span:6}} xs={{span:6}} className={"icon-secondary"}>
                                <img src={data.img_3} alt="Service icon" />
                            </Col>
                        </Row>
                    </div>
                    <h4>{data.title}</h4>
                    <p>{data.desc}</p>
                </div>
            );
        }
    );

    return (
        <div className="skills-slider pb-115">
            <Container>
                <Row>
                    <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                        <div className="heading-part-skills">
                            <div className="heading-part">
                                <h1>What I do</h1>
                                <p>take a look</p>
                            </div>
                        </div>
                    </Col>
                    <Row>
                        <Col lg={{span:6}} md={{span:6}} sm={{span:12}} xs={{span:12}}>
                            <div className="services-slider-images">
                                <img className={`img-1 ${box[0]}`} src="/images/content/serv1.jpg" alt="Service" />
                                <img className={`img-2 ${box[1]}`} src="/images/content/serv2.png" alt="Service" />
                                <img className={`img-3 ${box[2]}`} src="/images/content/serv3.jpg" alt="Service" />
                                <img className={`img-4 ${box[3]}`} src="/images/content/serv4.jpeg" alt="Service" />
                            </div>
                        </Col>
                        <Col lg={{span:6}} md={{span:6}} sm={{span:12}} xs={{span:12}}>
                            <div className="services-slider-box">
                                { slider_content }
                            </div>
                        </Col>
                    </Row>
                </Row>
            </Container>
        </div>
    );
}

export default Slider;