import React, { useState }from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import { useListener } from 'react-bus';
// used styles
import '../../scss/portfolio/style.scss';

// used components;
import Gallery from './Gallery';
import Footer from '../layouts/Footer';

function Portfolio () {
    let initStyle = {
        right: '100%',
        display: 'none'
    };

    const [style, setStyle] = useState(initStyle);
    const [arrowStyle, setArrowStyle] = useState({});

    const showPortfolio = () =>{
        setStyle(style => {
            style = {
                right: '100%',
                animationName: 'showPortfolioPage',
            };
            return style;
        });
        setArrowStyle({});
        setTimeout(function () {
            setStyle(style => {
                style = {
                    right: '0',
                    display: 'block',
                };
                return style;
            });
        },2000);
    };

    const hidePortfolio = () =>{
        setStyle(style => {
            style = {
                top: '0',
                animationName: 'hidePortfolioPage'
            };
            return style;
        });
        setArrowStyle(arrowStyle => {
            arrowStyle = {
                animation: "rotation 2s  linear",
            };
            return arrowStyle;
        });
        setTimeout(function () {
            setStyle(style => {
                style = {
                    right: '100%',
                    display: 'none',
                };
                return style;
            });
        },2000);
    };

    useListener('show-portfolio', showPortfolio);
    return (
        <div className={"portfolio-part pt-115"} style={style}>
            <div className="arrow-close" onClick={hidePortfolio}>
                <img src="/images/arrows/left_arrow.png" alt="Close" style={arrowStyle} />
            </div>
            <Container>
                <Row>
                    <Col lg={{span: 12}} md={{span: 12}} sm={{span: 12}} xs={{span: 12}}>
                        <div className="heading-part-portfolio">
                            <div className="heading-part">
                                <h1>portfolio</h1>
                                <p>MY work</p>
                            </div>
                        </div>
                        <div className="portfolio-info-header mb-30">
                            <h2>Sites I've been working on</h2>
                        </div>
                    </Col>
                    <Gallery />
                </Row>
            </Container>
            <Footer />
        </div>
    );
}

export default Portfolio;