import React, { useState }from 'react';
import {Container, Row} from 'react-bootstrap';
import { useListener } from 'react-bus';
// used styles
import '../../scss/about/style.scss';

// used components;
import Content from './Content';
import Education from './Education';
import Experience from './Experience';
import Footer from '../layouts/Footer';

function About () {
    let initStyle = {
        top: '-100%',
        display: 'none'
    };

    const [style, setStyle] = useState(initStyle);
    const [arrowStyle, setArrowStyle] = useState({});

    const showAbout = () =>{
        setStyle(style => {
            style = {
                top: '-300%',
                animationName: 'showAboutPage',
            };
            return style;
        });
        setArrowStyle({});
        setTimeout(function () {
            setStyle(style => {
                style = {
                    top: '0',
                    display: 'block',
                };
                return style;
            });
        },2000);
    };

    const hideAbout = () =>{
        setStyle(style => {
            style = {
                top: '0',
                animationName: 'hideAboutPage'
            };
            return style;
        });
        setArrowStyle(arrowStyle => {
            arrowStyle = {
                animation: "rotation 2s  linear",
            };
            return arrowStyle;
        });
        setTimeout(function () {
            setStyle(style => {
                style = {
                    top: '-100%',
                    display: 'none',
                };
                return style;
            });
        },2000);
    };

    useListener('show-about', showAbout);
    useListener('hide-about', hideAbout);

    return (
        <div className={"about-part pt-115"} style={style}>
            <div className="arrow-close" onClick={hideAbout}>
                <img src="/images/arrows/down_arrow.png" alt="Close" style={arrowStyle} />
            </div>
            <Container>
                <Row>
                    <Content />
                    <Education />
                    <Experience />
                </Row>
            </Container>
            <Footer page={'about'}/>
        </div>
    );
}

export default About;