import React from 'react';
import { NavLink } from "react-router-dom";
import { Row, Col} from 'react-bootstrap';
import resources from '../../resources';
// used styles

// used components;

function Gallery () {

    let gallery_content = resources.portfolio.sites.map((data, index) => {
            return (
                <Col key={index} lg={{span:4}} md={{span:4}} sm={{span:4}} xs={{span:12}}>
                    <a className="image-open" href={data.url_1}>
                        <div className="portfolio-img">
                            <img src={data.img_1} alt="Portfolio" />
                            <div className="portfolio-text">
                                <h4>{data.name_1}</h4>
                                <p>{data.desc_1}</p>
                            </div>
                        </div>
                    </a>
                    <a className="image-open" href={data.url_2}>
                        <div className="portfolio-img">
                            <img src={data.img_2} alt="Portfolio" />
                            <div className="portfolio-text">
                                <h4>{data.name_2}</h4>
                                <p>{data.desc_2}</p>
                            </div>
                        </div>
                    </a>
                </Col>
            );
        }
    );

    return (
        <div className={"portfolio-gallery"}>
            <Row>
                {gallery_content}
                <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}} className="text-center">
                    <button className="btn-part" id="loadMore">load more</button>
                    <NavLink to={"/technology"} className="links tech-link">
                        <button className="btn-part" id="tech-portfolio">technology portfolio</button>
                    </NavLink>
                </Col>
            </Row>
        </div>
    );
}

export default Gallery;