import React from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import Moment from 'react-moment';
import { useBus } from 'react-bus';

// used styles
import '../../scss/layouts/style.scss';

function Footer (props) {

    const bus = useBus();

    const portfolio = (e) => {
        e.preventDefault();
        switch (props.page) {
            case 'about':
                bus.emit('hide-about', true);
                break;
            case 'contact':
                bus.emit('hide-contact', true);
                break;
            case 'skills':
                bus.emit('hide-skills', true);
                break;
            default:
                return null;
        }

        bus.emit('show-portfolio', true);
    };

    const date = new Date();

    return (
        <div className="footer pt-30 pb-30">
            <Container>
                <Row>
                    <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                        <p>Copyright © <Moment format="YYYY">{date}</Moment> <a href="/" onClick={portfolio}>Portfolio</a>. All Rights Reserved</p>
                        <ul>
                            <li>
                                <a href="/">
                                    <i className="fab fa-facebook-f"></i>
                                </a>
                            </li>
                            <li>
                                <a href="/">
                                    <i className="fab fa-google-plus-g"></i>
                                </a>
                            </li>
                            <li>
                                <a href="/">
                                    <i className="fab fa-linkedin-in"></i>
                                </a>
                            </li>
                        </ul>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default Footer;