import React from 'react';
import resources from "../../resources";

// used styles
import '../../scss/technology/style.scss';


function TechnologyList () {
    let { projects } = resources.technology;

    let list = projects.map(({name, technology, site_url, second_url, gitHub_url, gitHub_second_url, additionally}) => {
            return (
                <li key={name}>
                    <p className="project-name">
                        <a href={site_url} className="links tech-link" target={"_blanc"}>
                            {name}
                        </a>
                    </p>
                    { second_url !== "" ?
                        <p className="project-name">
                            <a href={second_url} className="links tech-link" target={"_blanc"}>
                                second link
                            </a>
                        </p>
                        : null
                    }
                    <p className="project-technology">
                        ( {technology} )
                    </p>
                    <p className="git-hub-link">
                        <img src="./images/github.png" alt="github"/> : <a href={gitHub_url} target={"_blanc"}>{gitHub_url}</a>
                    </p>
                    { gitHub_second_url !== "" ?
                        <p className="git-hub-link">
                            <img src="./images/github.png" alt="github"/> : <a href={gitHub_second_url} target={"_blanc"}>{gitHub_second_url}</a>
                        </p>
                        : null
                    }
                    { additionally !== "" ?
                        <p className="project-additionally">
                            <img src="./images/team.png" alt="team"/> : {additionally}
                        </p>
                        : null
                    }
                </li>
            )
        }
    );

    return (
        <div className="technology-list-container">
            <ul>
                {list}
            </ul>
        </div>
    );
}

export default TechnologyList;