import React from 'react';
import { Row, Col} from 'react-bootstrap';
import resources from '../../resources';

function Education (){

    let education_content = resources.about.education.map((data, index) => {
        if (index%2 === 0 ) {
            return (
                <Col key={index} lg={{span:6, offset: 6}} md={{span:12, offset: 0}} sm={{span:12, offset: 0}} xs={{span:12, offset: 0}}>
                    <div className="about-learn-right">
                        <div className="about-learn-year">
                            <h1>{data.year}</h1>
                        </div>
                        <div className="about-learn-info">
                            <h4>{data.name}</h4>
                            <span>{data.city}</span>
                            <p>{data.desc}</p>
                        </div>
                    </div>
                </Col>
            );
        }
            return (
                <Col key={index} lg={{span:6}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                    <div className="about-learn-left">
                        <div className="about-learn-year">
                            <h1>{data.year}</h1>
                        </div>
                        <div className="about-learn-info">
                            <h4>{data.name}</h4>
                            <span>{data.city}</span>
                            <p>{data.desc}</p>
                        </div>
                    </div>
                </Col>
            );
        }
    );

    return (
        <div className="about-education pb-115">
            <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                <div className="heading-part-content">
                    <div className="heading-part">
                        <h1>Education</h1>
                        <p>learning</p>
                    </div>
                </div>
                <Col className="about-learn-section">
                    <Row>
                        { education_content }
                    </Row>
                </Col>
            </Col>
        </div>
    );
}

export default Education;