import React from 'react';
import { useBus } from 'react-bus';

function Menu (){
    const bus = useBus();
    const about = (e) => {
        e.preventDefault();
        bus.emit('show-about', true);
    };

    const contact = (e) => {
        e.preventDefault();
        bus.emit('show-contact', true);
    };

    const skills = (e) => {
        e.preventDefault();
        bus.emit('show-skills', true);
    };

    const portfolio = (e) => {
        e.preventDefault();
        bus.emit('show-portfolio', true);
    };

    return (
            <div className="menu-part">
                <a href="/" onClick={about} className="prt_top">about</a>
                <a href="/" onClick={contact} className="prt_right">contact</a>
                <a href="/" onClick={skills} className="prt_bottom">skills</a>
                <a href="/" onClick={portfolio} className="prt_left">portfolio</a>
            </div>
    );
}

export default Menu;