import React from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import {useBus} from 'react-bus';
import resources from '../../resources';

function Content (){

    const bus = useBus();

    const contact = (e) => {
        e.preventDefault();
        bus.emit('hide-about', true);
        bus.emit('show-contact', true);
    };

    const skills = (e) => {
        e.preventDefault();
        bus.emit('hide-about', true);
        bus.emit('show-skills', true);
    };

    return (
        <Container>
            <Row>
                <div className="about-info pb-80">
                    <Col lg={{span:6}} md={{span:6}} sm={{span:12}} xs={{span:12}}>
                        <div className="about-img">
                            <img src="/images/about.jpg" alt="About" />
                        </div>
                    </Col>
                    <Col lg={{span:6}} md={{span:6}} sm={{span:12}} xs={{span:12}}>
                        <div className="about-heading-2">
                            <div className="heading-part pt-50">
                                <h1>About ME</h1>
                                <p>WHo AM i</p>
                            </div>
                        </div>
                        <div className="about-details">
                            <p>
                                {resources.about.text}
                                <a href="/" className="skills-link" onClick={skills}>skills</a>
                                {resources.about.text_2}
                            </p>
                            <a href="/pdf/Aram_Minasyan.pdf" className="btn-part" download>Download Resume</a>
                            <a href="/" className="btn-part" onClick={contact}>Hire Me</a>
                        </div>
                    </Col>
                </div>
            </Row>
        </Container>
    );
}

export default Content;