import React from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import resources from "../../resources";
// used styles

// used components;

function MySkills () {

    let chart_content = resources.skills.chart.map((data, index) => {
            return (
                <Col key={index} lg={{span: 3}} md={{span: 3}} sm={{span: 12}} xs={{span: 12}} className={"text-center"}>
                    <div className="single-chart">
                        <svg viewBox="0 0 36 36" className={`circular-chart ${data.type}`}>
                            <path className="circle-bg" d="M18 2.0845
                                a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
                            />
                            <path className="circle" strokeDasharray={`${data.percent} 100`} d="M18 2.0845
                                a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
                            />
                            <text x="18" y="20.35" className="percentage">{data.percent}%</text>
                        </svg>
                    </div>
                    <h5>{data.name}</h5>
                </Col>
            );
        }
    );
    return (
        <div className="my-skills-part pt-115 pb-115">
            <Container>
                <Row>
                    <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                        <div className="heading-part-my-skills">
                            <div className="heading-part">
                                <h1>My Skills</h1>
                                <p>Expert in</p>
                            </div>
                        </div>
                    </Col>
                    <div id={"canvas"}>
                        <Row>
                            { chart_content }
                        </Row>
                    </div>
                </Row>
            </Container>
        </div>
    );
}

export default MySkills;