import React, { useState }from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import { useListener } from 'react-bus';
// used styles
import '../../scss/contact/style.scss';

// used components;
import Email from './Email';
import Details from './Details';
import Footer from '../layouts/Footer';

function Contact () {
    let initStyle = {
        left: '100%',
        display: 'none'
    };

    const [style, setStyle] = useState(initStyle);
    const [arrowStyle, setArrowStyle] = useState({});

    const showContact = () =>{
        setStyle(style => {
            style = {
                animationName: 'showContactPage',
                left: '100%',
            };
            return style;
        });
        setArrowStyle({});
        setTimeout(function () {
            setStyle(style => {
                style = {
                    left: '0',
                    display: 'block',
                };
                return style;
            });
        },2000);
    };

    const hideContact = () =>{
        setStyle(style => {
            style = {
                animationName: 'hideContactPage'
            };
            return style;
        });
        setArrowStyle(arrowStyle => {
            arrowStyle = {
                animation: "rotation 2s  linear",
            };
            return arrowStyle;
        });
        setTimeout(function () {
            setStyle(style => {
                style = {
                    left: '100%',
                    display: 'none',
                };
                return style;
            });
        },2000);
    };

    useListener('show-contact', showContact);
    useListener('hide-contact', hideContact);

    return (
        <div className={"contact-part pt-115"}  style={style}>
            <div className="arrow-close" onClick={hideContact}>
                <img src="/images/arrows/right_arrow.png" alt="Close" className="prt_close" style={arrowStyle} />
            </div>
            <Container>
                <Row>
                    <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                        <div className="heading-part-content">
                            <div className="heading-part">
                                <h1>get in touch</h1>
                                <p>contact us</p>
                            </div>
                        </div>
                    </Col>
                    <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                        <div className="contact-info">
                            <div className="contact-info-header">
                                <h1>How can i help you ?</h1>
                            </div>
                            <div className="contact-form">
                                <Row>
                                    <Email />
                                </Row>
                            </div>
                        </div>
                    </Col>
                    <Details />
                </Row>
            </Container>
            <Footer page={'contact'}/>
        </div>
    );
}

export default Contact;