import React, { useState } from 'react';
import { useListener } from 'react-bus';
// used styles
import '../../scss/skills/style.scss';

// used components;
import Slider from './Slider';
import Separator from './Separator';
import MySkills from './MySkills';
import Footer from '../layouts/Footer';

function Skills () {

    let initStyle = {
        top: '100%',
        display: 'none'
    };

    const [style, setStyle] = useState(initStyle);
    const [arrowStyle, setArrowStyle] = useState({});

    const showSkills = () =>{
        setStyle(style => {
            style = {
                animationName: 'showSkillsPage',
                display: 'block',
                top: '100%'
            };
            return style;
        });
        setArrowStyle({});
        setTimeout(function () {
            setStyle(style => {
                style = {
                    top: '0',
                    display: 'block',
                };
                return style;
            });
        },2000)
    };

    const hideSkills = () =>{
        setStyle(style => {
            style = {
                top: '0',
                animationName: 'hideSkillsPage'
            };
            return style;
        });
        setArrowStyle(arrowStyle => {
            arrowStyle = {
                animation: "rotation 2s  linear",
            };
            return arrowStyle;
        });
        setTimeout(function () {
            setStyle(style => {
                style = {
                    display: 'none',
                };
                return style;
            });
        },2000)
    };

    useListener('show-skills', showSkills);
    useListener('hide-skills', hideSkills);

    return (
        <div className={"skills-part pt-115"} style={style}>
            <div className="arrow-close" onClick={hideSkills}>
                <img src="/images/arrows/up_arrow.png" alt="Close" className="Close" style={arrowStyle} />
            </div>
            <Slider />
            <Separator />
            <MySkills />
            <Footer page={'skills'}/>
        </div>
    );
}

export default Skills;