import React,{ useState } from 'react';
import {  Form, Button, Alert, Row, Col }  from 'react-bootstrap';
import useForm from "react-hook-form";
import { ToastContainer } from 'react-toastify';

function Email  ()  {
    // Component local data
    const [error] = useState('');
    // Event bus part end

    // Form part
    const { register, handleSubmit, errors } = useForm();
    const onSubmit = data => {
        console.log(data);
        // todo need add email sending functionality
        // toast.success("Your registration has forgiven successfully !");
        document.getElementById("email").submit();
    };

    // Form part END

    return (
        <>
            <ToastContainer
                position="top-center"
                autoClose={4000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnVisibilityChange
                draggable
                pauseOnHover
            />
            <Form id="email" onSubmit={handleSubmit(onSubmit)} action="https://formspree.io/xzbpwkjk" method="POST">
                <Row className={"form-info-row"}>
                    <Col lg={{span:6}} md={{span:6}} sm={{span:12}} xs={{span:12}}>
                        <Form.Group controlId="formName">
                            <Form.Control type="text" placeholder="Your Name" name="name" ref={register({required: true})}/>
                            <Form.Control.Feedback type="invalid" >
                                {errors.name && 'Name field is required !'}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Col>
                    <Col lg={{span:6}} md={{span:6}} sm={{span:12}} xs={{span:12}}>
                        <Form.Group controlId="formLoginEmail">
                            <Form.Control type="email" placeholder="Your Email" name="email" ref={register({required: true, pattern: /^\S+@\S+$/i})}/>
                            <Form.Control.Feedback type="invalid" >
                                {errors.email && 'Incorrect email address.'}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Col>
                </Row>
                <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                    <Form.Group controlId="formLoginSubject">
                        <Form.Control type="text" placeholder="Subject" name="subject" ref={register({ required: true})}/>
                        <Form.Control.Feedback type="invalid">
                            {errors.subject && 'Subject field is required !'}
                        </Form.Control.Feedback>
                    </Form.Group>
                </Col>
                <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                    <Form.Group controlId="formLoginMessage">
                        <Form.Control as="textarea" rows="4" placeholder="Message" name="message" ref={register({ required: true})}/>
                        <Form.Control.Feedback type="invalid">
                            {errors.message && 'Message field is required !'}
                        </Form.Control.Feedback>
                    </Form.Group>
                </Col>
                <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                    <Form.Group>
                        {error !== '' &&
                        <Alert key="fb-error" variant="danger">
                            {error}
                        </Alert>
                        }
                    </Form.Group>
                </Col>
                <Col lg={{span:12}} md={{span:12}} sm={{span:12}} xs={{span:12}}>
                    <Button className={"form-info-btn"} variant="primary" type="submit" form="email">Send</Button>
                </Col>
            </Form>
        </>
    );

};
export default Email;